.contact {
  margin: 50px 0px;
  margin-left: 120px;
  height: 90vh;
  padding-left: 110px;
}

.card0 {
  box-shadow: 0px 4px 8px 0 #757575;
  border-radius: 0px;
}

.card2 {
  margin: 0px 40px;
}

.card1 .image {
  padding: 30px;
  margin-top: 30px;
  height: 100%;
  width: 100%;
}

.border-line {
  border-right: 1px solid #e0e0e0;
  height: 100%;
}

.line {
  height: 1px;
  width: 45%;
  background-color: #e0e0e0;
  margin-top: 10px;
}

.or {
  display: flex;
  width: 10%;
  font-weight: bold;
}

::placeholder {
  color: #bdbdbd;
  opacity: 1;
  font-weight: 300;
}

input,
textarea {
  padding: 10px 12px;
  border: 1px solid lightgray;
  border-radius: 2px;
  margin-top: 5px;
  width: 100%;
  color: #138781;
  font-size: 1rem;
  letter-spacing: 1px;
}

input:focus,
textarea:focus {
  box-shadow: none !important;
  outline: 1px solid #138781 !important;
  outline-width: 0px;
}

.button:focus {
  box-shadow: none !important;
  outline-width: 0px;
}

.button {
  background-color: #1e1e2c;
  color: white;
  border-radius: 5px;
  margin-top: 15px;
  padding: 10px;
}

.button:hover {
  background-color: black;
  color: white;
}

/* Mobile View */
@media (max-width: 600px) {
  .contact {
    margin: 0px 0px;
    margin-left: 20px;
    padding-left: 0px;
    margin-top: 330px;
    height: 150vh;
    width: 600px;
  }

  .contact input,
  .contact textarea {
    margin: 10px;
    width: 95%;
  }
}
